import { useEffect, useState } from 'react';
import axios from 'axios';
import './Maze.css';




const Maze: React.FC<{ maze: { row: number, col: number, tiles: string }, apiUrl: string }> = (props) => {
    const [win, setWin] = useState(0);

    const [playerState, setPlayerState] = useState({
        x: 0,
        y: 0,
        d: 0,
        dir: 0,
        move: 0,
        cmd: ""
    });

    const [targetState, setTargetState] = useState({
        x: 0,
        y: 0,
    });

    useEffect(() => {
        let pid = props.maze.tiles.match(/[LRUD]/)?.index ?? 0;

        setPlayerState(prevState => ({
            ...prevState,
            x: Math.floor(pid / props.maze.col),
            y: pid % props.maze.col,
            d: "NRDL".indexOf(props.maze.tiles[pid]),
        }));

        let tid = props.maze.tiles.indexOf('T');

        setTargetState({
            x: Math.floor(tid / props.maze.col),
            y: tid % props.maze.col,
        });
    }, [props.maze]);

    const validPosition = (x: number, y: number) => {
        if (!(x >= 0 && x < props.maze.row && y >= 0 && y < props.maze.col)) return false;
        if (Math.abs(playerState.x - x) < 0.2 && Math.abs(playerState.y - y) < 0.2 && props.maze.tiles[x * props.maze.row + y] === 'T' && win === 0) {
            alert('你赢了');
            setWin(1);
        }
        return " LRUDT".includes(props.maze.tiles[x * props.maze.col + y]);
    }

    const turn = async () => {
        if (playerState.dir % 4 === 0) {
            setPlayerState(prevState => ({
                ...prevState,
                d: (prevState.d + prevState.dir / 4 + 4) % 4,
                dir: 0
            }))
        } else {
            let promise = new Promise(resolve => setTimeout(() => {
                setPlayerState(prevState => ({
                    ...prevState,
                    dir: prevState.dir + (prevState.dir < 0 ? -1 : 1)
                }));
            }, 50));
            await promise;
            turn();
        }
    };

    const moveForward = async () => {
        if (playerState.move !== 0) {
            let flag = false;
            if (playerState.d === 0)
                flag = validPosition(Math.ceil(playerState.x) - 1, playerState.y);
            if (playerState.d === 1)
                flag = validPosition(playerState.x, Math.floor(playerState.y) + 1);
            if (playerState.d === 2)
                flag = validPosition(Math.floor(playerState.x) + 1, playerState.y);
            if (playerState.d === 3)
                flag = validPosition(playerState.x, Math.ceil(playerState.y) - 1);

            let promise = new Promise(resolve => setTimeout(() => {
                setPlayerState(prevState => ({
                    ...prevState,
                    x: prevState.x + (flag ? playerState.d === 0 ? (-1 / 8) : playerState.d === 2 ? (1 / 8) : 0 : 0),
                    y: prevState.y + (flag ? playerState.d === 3 ? (-1 / 8) : playerState.d === 1 ? (1 / 8) : 0 : 0),
                    move: Math.max(0, prevState.move - 1)
                }));
            }, 50));
            await promise;
            moveForward();
        }
    };

    const fetchCmd = () => {
        axios.get(props.apiUrl + 'cmd')
            .then(function (response) {
                // handle success
                setPlayerState(prevState => ({
                    ...prevState,
                    cmd: response.data
                }));
            });
    }

    useEffect(() => {
        if (win === 0)
            turn();
    }, [playerState.dir]);

    useEffect(() => {
        if (win === 0)
            moveForward();
    }, [playerState.move]);

    useEffect(() => {
        if (win === 0 && playerState.cmd.length > 0) {
            setTimeout(() => {
                if (playerState.cmd[0] === 'L') {
                    setPlayerState(prevState => ({
                        ...prevState,
                        dir: -1
                    }));
                }
                if (playerState.cmd[0] === 'R') {
                    setPlayerState(prevState => ({
                        ...prevState,
                        dir: 1
                    }));
                }
                if (playerState.cmd[0] === 'F') {
                    setPlayerState(prevState => ({
                        ...prevState,
                        move: 8
                    }));
                }
                setPlayerState(prevState => ({
                    ...prevState,
                    cmd: prevState.cmd.slice(1)
                }));
            }, 500);
        }
    }, [playerState.cmd]);

    // set tiles 
    let tiles = [...props.maze.tiles].map((t, i) => {
        let stystr = '0 0';
        if (t === '#')
            stystr = '-200px -150px';
        else if (t === 'v')
            stystr = '-150px -50px';
        else if (t === '@')
            stystr = '-150px 0';
        else if (t === '<')
            stystr = '0 -150px';
        else if (t === '>')
            stystr = '-50px -150px';
        return <div
            className="maze-tile"
            key={i}
            style={{
                backgroundPosition: `${stystr}`,
                height: `${100 / props.maze.row}%`,
                width: `${100 / props.maze.col}%`,
            }}
        />;
    });

    return (
        <div>
            <div id="maze" style={{
                backgroundSize: `${800 / props.maze.col}% ${800 / props.maze.row}%`,
                width: `${props.maze.col * 50}px`,
                height: `${props.maze.row * 50}px`,
            }}>
                {tiles}
                <div
                    className="player"
                    style={{
                        marginLeft: `${playerState.y * 50 - 5}px`,
                        top: `${playerState.x * 50 - 30}px`,
                        backgroundPosition: `${-(playerState.d * 4 + playerState.dir + 16) % 16 * 60}px 0`,
                        display: `${playerState.move ? "none" : "block"}`
                    }}
                />
                <div
                    className="move-player"
                    style={{
                        marginLeft: `${playerState.y * 50 - 5}px`,
                        top: `${playerState.x * 50 - 30}px`,
                        backgroundPosition: `${-playerState.d * 60}px ${playerState.move * 80}px`,
                        display: `${playerState.move ? "block" : "none"}`
                    }}
                />
                <div
                    className="target"
                    style={{
                        marginLeft: `${targetState.y * 50}px`,
                        top: `${targetState.x * 50}px`,
                    }}
                />
            </div>
            <button className="exec-button" onClick={fetchCmd}>运行</button>
        </div>
    );
}

export default Maze;