import axios from 'axios';
import React, { useEffect, useState } from 'react';
import Maze from './components/Maze';



const App: React.FC<{ apiUrl: string }> = (props) => {
  const [mazeState, setMazeState] = useState({
    row: 4,
    col: 8,
    tiles: "# #@@@ ##    T ## R    ##@@@@@@#"
  });

  useEffect(() => {
    axios.get(props.apiUrl + 'map')
      .then((response) => {
        setMazeState({
          row: response.data.row,
          col: response.data.col,
          tiles: response.data.map
        });
      });
  }, []);
  return (
    <div className="App">
      {/* <small>You are running this application in <b>{process.env.NODE_ENV}</b> mode.</small> */}
      <Maze maze={mazeState} apiUrl={props.apiUrl} />
    </div>
  );
}

export default App;
